:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

a {
  text-decoration: none;
}

p {
  margin: 0;
}

.search-container {
  width: 100%;
  background-color: #3f57b3;
  padding-top: 10px;
  padding-bottom: 10px;
  position: fixed;
}

.search-form {
  width: 100%;
  justify-content: center;
  display: flex;
}

.search-form input {
  width: 20%;
  font: inherit;
  background-color: #fff;
  border: none;
  border-radius: 4px 0 0 4px;
  outline: none;
  padding: 8px 10px;
}

.search-form button {
  width: 4em;
  cursor: pointer;
  background-color: #c8c8c8;
  border: none;
  border-radius: 0 4px 4px 0;
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-form button:hover, .search-form button:focus {
  background-color: #b4b4b4;
  transition: all .3s;
}

.gallery__link {
  min-width: 260px;
  border: 2px solid #f1eeee;
  flex-basis: calc(25% - 20px);
  margin-top: 20px;
  margin-left: 20px;
  transition: all .3s;
  overflow: hidden;
}

.gallery__link:hover {
  cursor: zoom-in;
}

.info {
  justify-content: space-around;
  padding: 10px;
  font-size: 12px;
}

.info-item {
  color: #000;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.info-item:not(:first-child) {
  margin-left: 5px;
}

.load-more.is-hidden {
  display: none;
}

.info {
  flex-wrap: wrap;
  display: flex;
}

.gallery {
  max-width: 1140px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  grid-auto-rows: 260px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding-top: 60px;
  display: grid;
}

.gallery__item {
  width: 260px;
  flex-direction: column;
  justify-content: space-evenly;
  display: flex;
}

.gallery__image:hover {
  transform: scale(1.03);
}

.gallery__image {
  height: 190px;
  object-fit: cover;
  display: block;
}

.load-more {
  font: inherit;
  color: #fff;
  cursor: pointer;
  background-color: #3f57b3;
  border: none;
  border-radius: 5px;
  margin: 25px auto 0;
  padding: 8px 20px;
  display: block;
  box-shadow: 0 1px 2px rgba(60, 64, 67, .3), 0 2px 6px 2px rgba(60, 64, 67, .15);
}

